<template>
  <div>
    <CartList v-if="!goCheck" @goChange='changeValue' :page="'home'" />
    <CheckOut v-if="goCheck" />
  </div>
</template>


<script>
  import CartList from '@/components/dashboard/main/cartModal/cartList.vue'
  import CheckOut from '@/components/dashboard/main/CheckOut.vue'
  export default {
    props: {},
    data() {
      return {
        goCheck: false,
        page: 'home',
      }
    },
    components: {
      CartList,
      CheckOut
    },
    methods: {
      changeValue() {
        this.goCheck = !this.goCheck
      }
    },
    created() {

    }
  }

</script>
