<template>
  <div class="modal fade" id="Modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span>新增優惠券</span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-12">

              <div class="form-group">
                <label for="title">標題</label>
                <input type="text" class="form-control" id="title" placeholder="請輸入優惠券名稱" v-model="coupon.title">
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="origin_price">折扣百分比</label>
                  <input type="number" class="form-control" id="origin_price" placeholder="請輸入折扣百分比"  v-model="coupon.percent">
                </div>
                <div class="form-group col-md-6">
                  <label for="price">到期日</label>
                  <input type="date" class="form-control" id="price" placeholder="請輸入期限"  v-model="coupon.due_date">
                </div>
              </div>

              <div class="form-row">
                <div class="form-group">
                  <label for="price">優惠券序號</label>
                  <input type="text" class="form-control" id="price" placeholder="請輸入序號"  v-model="coupon.code">
                </div>
              </div>


              <div class="form-group">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" id="is_enabled"  v-model="coupon.is_enabled" :true-value='1' :false-value='0'>
                  <label class="form-check-label" for="is_enabled">
                    是否啟用
                  </label>
                </div>
              </div>

            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-primary" @click="buildCoupon">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    props:['coupon','isNew','click'],
    data() {
      return {
        status:{
          fileLoading: false
        }
      }
    },
    methods: {
      buildCoupon() {
        const vm = this;
        let api = `${process.env.VUE_APP_API}/api/${vm.click}/admin/coupon`;
        let method= 'post'
        if (!vm.isNew){
          api = `${process.env.VUE_APP_API}/api/${vm.click}/admin/coupon/${vm.coupon.id}`;
          method= 'put'
        }

        this.$http[method](api, { data: vm.coupon}).then((response) => {
          console.log("buildCoupon success", response);
          if (response.data.success){
            $('#Modal').modal('hide')
          } else {
            console.log("buildCoupon failed, try again");
            vm.$bus.$emit('message:push', response.data.message, 'danger')
            $('#Modal').modal('hide')
          }
        this.$emit('get-coupon')
        })
      },

    }
  }

</script>
