import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import ShowCustProducts from '@/components/homeModal/ShowCustProducts.vue'
import ToCartPage from '@/components/homeModal/ToCartPage.vue'

import Login from './views/Login.vue'

import Admin from './views/Admin.vue'
import Coupons from '@/components/dashboard/main/Coupons.vue'
import Products from '@/components/dashboard/main/Products.vue'
import Orders from '@/components/dashboard/main/Orders.vue'
import eProdoucts from '@/components/dashboard/main/eProdoucts.vue'
import CheckOut from '@/components/dashboard/main/CheckOut.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '*',
      redirect: '/home'
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {
          path: 'home',
          name: 'ShowCustProducts',
          component: ShowCustProducts
        },
        {
          path: 'cart',
          name: 'ToCartPage',
          component: ToCartPage
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      children: [
        {
          path: 'products',
          name: 'Products',
          component: Products,
          meta: { requiresAuth: true }
        },
        {
          path: 'coupons',
          name: 'Coupons',
          component: Coupons,
          meta: { requiresAuth: true }
        },
        {
          path: 'orders',
          name: 'Orders',
          component: Orders,
          meta: { requiresAuth: true }
        },
        {
          path: 'eproducts',
          name: 'eProdoucts',
          component: eProdoucts
        },
        {
          path: 'check',
          name: 'CheckOut',
          component: CheckOut
        }
      ]
    }
  ]
})
